.Hangman {
  width: 60rem;
  color: #fff;
  text-align: center;
  -webkit-transform: scale(0.44);
      -ms-transform: scale(0.44);
          transform: scale(0.44);
}

.Hangman img {
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06);
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.06);
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 1rem;
  margin-top: 0;
}

.Hangman-remaining {
  font-size: 2.2rem;
  padding-top: 3rem;
}

.Hangman-word {
  letter-spacing: 2rem;
  margin: 0.4em -1rem 0.2em 0rem;
  font-size: 4rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 25rem;
  margin-top: 1rem;
}

.btn {
  border: none;
  font-family: inherit;
  font-size: 2rem;
  cursor: pointer;
  width: 3.5rem;
  height: 3.5rem;
  margin: 1rem 8px 8px 8px;
  letter-spacing: 1px;
  outline: none;
  background: #ffc107;
  color: #fff;
  -webkit-box-shadow: 0 6px #f8f8f8;
          box-shadow: 0 6px #f8f8f8;
  border-radius: 5px;
  padding-top: 4px;
}

.btn:hover {
  background-color: #ff9800;
}

.btn:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px #9e9e9e;
          box-shadow: 0 6px #9e9e9e;
}

.reset-btn {
  display: inline-block;
  color: #fff;
  background-color: #ff9800;
  width: 15rem;
  border: none;
  font-size: 1.6rem;
  font-weight: bolder;
  text-transform: uppercase;
  padding: 1rem 2rem;
  margin: 5rem 2rem 0 0;
  cursor: pointer;
}

.hurray {
  font-size: 2.4rem;
  margin: 5rem 0;
}



